import React, { useState } from 'react';
import styled from 'styled-components';
import { Reset } from 'styled-reset';
import { useDispatch, useSelector } from 'react-redux';
import { Map } from 'immutable';
import { withRouter } from 'react-router-dom';

import Graph, { GraphType, ResourceType } from '../models/Graph';
import { AddGraph } from '../actions/GraphActions';
import { State } from '../reducers';
import SearchDistribution from '../data/search/Distribution';
import SearchEvaluation from '../data/search/Evaluation';

import PieChart from './Graphs/Pie';
import RaderChart from './Graphs/Radar';
import SearchForm from './SearchForm';
import ControllArea from './ControllArea';
import Modal from './Modal';

const evalQuestions = {
  問1: '授業の準備は十分にされていたと思いますか',
  問2: '教員の説明や授業の進め方は適切でしたか',
  問3: '授業を通じて、この科目に関連する分野への興味や関心が高まりましたか',
  問4: '総合的に判断して、この授業を受講してよかったと思いますか',
  問8: '私はこの科目にはもともと興味があった',
  問9: '私はこの授業の予習・復習・課題のために授業外で相当時間の学習を行った',
  問10: 'この授業はシラバスに沿って計画的に行われていた',
  問11: '授業担当者の話し方は聞き取りやすかった',
  問12: 'この授業で使われた教科書，配布資料は適切であった',
  問13: 'この授業における黒板，ビデオ，パソコンなどの使い方は効果的であった',
  問14: 'この授業では学期途中に学生の意見・要望が適切に収集された',
  問15: 'この授業の内容はよく理解できた',
  問16: 'この授業により，新しい知識や考え方が修得できた',
  問17: 'この授業により，さらに深く勉強したくなった',
};

const OnChartAdded = records => {
  const addedNode = records[0].addedNodes[0];
  if (typeof addedNode === 'undefined') return;

  const chartSVG = addedNode.lastElementChild.firstElementChild.firstElementChild.lastElementChild;
  if (chartSVG === null) return;
  const tooltipLabelParent = chartSVG.firstElementChild.firstElementChild.lastElementChild;
  if (tooltipLabelParent === null) return;
  for (const item of tooltipLabelParent.children) {
    const qTitle = item.firstElementChild.innerHTML;
    item.addEventListener('mouseover', function(event) {
      const discriptArea = addedNode.getElementsByClassName('chart-discription')[0];
      discriptArea.innerHTML = evalQuestions[qTitle];
    });
  }
};

type Props = {
  match: any;
  location: any;
  history: any;
};

const Course: React.FC<Props> = props => {
  const [controllState, SetControllState] = useState({
    type: ResourceType.Distribution,
    year: '2018',
    open: false,
    modalSubjects: [Map({ '': '' })],
  });

  const target = document.getElementById('chart-area');
  if (target !== null) {
    const mo = new MutationObserver(OnChartAdded);
    mo.observe(target, { childList: true });
  }

  // eslint-disable-next-line react/prop-types
  // props.history.listen((location, action) => {
  //   // location is an object like window.location
  //   if (location.pathname === '/') {
  //     console.log(action, location.pathname);
  //     const target = document.getElementById('chart-area');
  //     if (target !== null) {
  //       const mo = new MutationObserver(OnChartAdded);
  //       mo.observe(target, { childList: true });
  //     }
  //   }
  // });

  const graphs = useSelector((state: State) => state.graphs);
  const dispatch = useDispatch();
  const handleOpen = subjects => {
    SetControllState({ ...controllState, open: true, modalSubjects: subjects });
  };
  const handleClose = () => {
    SetControllState({ ...controllState, open: false });
  };
  const OnModalClick = subjectID => {
    handleClose();
    const IMsubject = controllState.modalSubjects.find(IMsub => {
      // const subject = IMsubject.toObject();
      return IMsub.toObject()['科目番号'] === subjectID;
    });

    if (typeof IMsubject === 'undefined') {
      return;
    }

    const subject = IMsubject.toObject();
    // console.log(Graph.CreateGraph(subject, controllState.year, controllState.type, Number(subject['履修者数'])));
    dispatch(AddGraph(Graph.CreateGraph(subject, controllState.year, controllState.type, Number(subject['履修者数']))));
  };

  const OnSelected = suggestion => {
    const subjects = Graph.GetSubject(controllState.year, suggestion.label, controllState.type);
    if (subjects.length >= 2) {
      handleOpen(subjects);
      return;
    }
    const subject = subjects[0].toObject();

    dispatch(AddGraph(Graph.CreateGraph(subject, controllState.year, controllState.type, Number(subject['履修者数']))));
  };

  return (
    <>
      <Reset />

      <InputArea>
        <SearchForm
          suggestions={
            controllState.type === ResourceType.Distribution
              ? SearchDistribution[controllState.year]
              : SearchEvaluation[controllState.year]
          }
          onSelected={OnSelected}
          searchKey={'label'}
        />
      </InputArea>
      <ControllArea
        onTypeChange={value => SetControllState({ ...controllState, type: value as ResourceType })}
        onYearChange={value => SetControllState({ ...controllState, year: value })}
      />
      <ChartArea id={'chart-area'}>
        {graphs.size > 1 &&
          graphs.slice(1).map(
            (graph: Graph): JSX.Element => {
              switch (graph.type) {
                case GraphType.Pie:
                  return (
                    <PieChart
                      width='400px'
                      height='400px'
                      data={graph.data}
                      title={graph.title}
                      studentCount={graph.studentCount}
                      year={graph.year}
                      key={graph.key}
                      id={graph.key}
                    />
                  );
                case GraphType.Radar:
                  return (
                    <RaderChart
                      width='400px'
                      height='400px'
                      data={graph.data}
                      keys={[graph.title]}
                      key={graph.key}
                      id={graph.key}
                    />
                  );
                default:
                  return <></>;
              }
            },
          )}
      </ChartArea>
      <Modal
        open={controllState.open}
        onClose={handleClose}
        subjects={controllState.modalSubjects}
        onClick={OnModalClick}
      ></Modal>
    </>
  );
};

const InputArea = styled.div`
  /* width: 100%; */
  padding: 10px 30px;
  background-color: #ddd;
  display: flex;
  justify-content: center;
`;

const ChartArea = styled.div`
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
`;
export default withRouter(Course);
